body, html {
  margin: 0;
  padding: 0;
  height: 100%;
  font-family: 'Arial', sans-serif;
  background-color: #f4f4f4;
  width: 100%;
  flex-direction: column;
}

.nav-links {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
}

.nav-links ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
  gap: 5px;
}

.nav-links a, .nav-links button {
  text-decoration: none;
  color: #ffffff;
  background-color: transparent;
  cursor: pointer;
  font-size: calc(10px + 0.4vw);
  transition: color 0.3s ease;
}


.nav-links a:hover, .nav-links button:hover {
  color: #e0e0e0;
}

.content {
  width: 100%;
  max-width: 1200px;
  background: #ffffff;
  padding: 40px;
  border-radius: 12px;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
  margin-top: 80px;
  text-align: center;
}

.events-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  margin-top: 20px;
  max-width: 100%;
  padding: 0 20px; 
}

.event-card {
  height: 400px;
  max-width: 250px;
  perspective: 1000px;
  cursor: pointer;
}

.event-card-inner {
  position: relative;
  width: 100%;
  height: 400px;
  transition: transform 0.6s;
  transform-style: preserve-3d;
}

.event-card.flipped .event-card-inner {
  transform: rotateY(180deg);
}

.event-card-front, .event-card-back {
  position: absolute;
  width: 100%;
  height: 400px;
  backface-visibility: hidden;
  border-radius: 12px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.event-card-front {
  background-color: #ffffff;
}

.event-card-back {
  background-color: #1A1B6B;
  color: #ffffff;
  transform: rotateY(180deg);
  height: 400px;
}

.event-poster {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.event-details {
  padding: 20px;
}

.event-details h3 {
  margin: 0;
  font-size: 20px;
  color: #333333;
}

.event-details p {
  margin: 10px 0;
  color: #666666;
}

.button, .booking-button, .proceed-button, .like-button {
  border-radius: 5px;
  border: none;
  padding: 8px 16px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  font-size: 14px;
}

.booking-button {
  background-color: #f0ad4e;
  color: #ffffff;
  margin-top: 10px;
}

.booking-button:hover {
  background-color: #d28e22;
}

.proceed-button {
  background-color: #28a745;
  color: white;
  margin-top: 10px;
}

.proceed-button:hover {
  background-color: #218838;
}

.booking-dropdown {
  width: 100%;
  margin-top: 10px;
  background-color: #d28e22; 
  color: white;
  text-align: center;
}

.payment-selection {
  margin-top: 20px;
  text-align: center;
}

.payment-selection select {
  width: 200px;
}

.payment-selection h3 {
  margin-bottom: 10px;
}

.like-button {
  background-color: #ff6b6b;
  color: white;
  margin-top: 10px;
}

.like-button:hover {
  background-color: #e05555;
}

.like-button:disabled {
  background-color: #d3d3d3;
  cursor: not-allowed;
}

.settings-dropdown {
  position: relative;
  display: inline-block;
}

.settings-dropdown button {
  background-color: #1A1B6B;
  color: rgb(52, 13, 132);
}

.settings-dropdown .dropdown-content {
  display: none;
  position: absolute;
  background-color: #1A1B6B;
  min-width: 160px;
  box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.2);
  z-index: 1;
  border: 2px solid #ffffff;
  border-radius: 4px;
}

.settings-dropdown:hover .dropdown-content {
  display: block;
}

.dropdown-content button {
  color: rgb(55, 138, 171);
  padding: 12px 16px;
  text-align: left;
  display: block;
  width: 100%;
  background: none;
}

.dropdown-content button:hover {
  background-color: #36a7c0;
}
